import { storage } from "./storage";
import JSONstat from "jsonstat";

const constants = window.configuration.data;

function queryService(category) {
  "use strict";

  // create the countries
  const countries = constants.countriesEu.concat(
    constants.countriesOthers,
    constants.countriesNonMembers,
    constants.countriesEfta
  );
  let countriesUrl = "";

  for (let a = 0, n = countries.length; a < n; a += 1) {
    countriesUrl += "&geo=" + countries[a];
  }

  // retrieve correct webservice endpoint depending on environment
  const jsonURL = constants["url_" + storage.NODE_ENV];

  // assemble URL
  const url =
    jsonURL +
    // constants.lang +
    // "/" +
    constants.dataUrls[category] +
    countriesUrl;

  return jQuery
    .ajax({
      type: "GET",
      url: url,
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      crossDomain: true,
      cache: true,
      timeout: 30000,
      processData: false,
      data: "",
    })
    .done(function (data) {
      storage.rawData[category] = data;
    })
    .fail(function (jqXHR, textStatus, errorThrown) {
      jQuery("#graph-container").empty();
      jQuery("#graph-container").append(
        "<div style='text-align: center; vertical-align: middle;'>There was an error retrieving data.<br />Please try again in a few minutes.</div>"
      );
      jQuery("#graph-container").text(
        "There was an error retrieving data. Please try again in a few minutes."
      );
      jQuery("#loader").hide();
    });
}

function extractData(countries, ds, paramObj, category) {
  "use strict";

  let data = [];

  let sex = constants.sex;

  for (let a = 0, n = countries.length; a < n; a += 1) {
    let value = {};
    for (let b = 0, m = sex.length; b < m; b += 1) {
      value.country = countries[a];
      paramObj.geo = countries[a];

      // check if we have a different name for female/male
      if (sex[b].indi_abbr) {
        // dataset doesn't use sex!
        paramObj.ind_type = sex[b].indi_abbr;
      } else {
        paramObj.sex = sex[b].abbr;
      } 
      // let cate = constants.categories[category];
      let result = 0;
      // paramObj[cate[0]] = cate[1];
      result = ds.Data(paramObj)[0].value;
      //
      if(!result){ // one year back 
        console.log(81,countries[a], ds.Data(paramObj)[0].value, paramObj.time, '/',sex[b].abbr)
      }

      value[sex[b].abbr] = result;
    }
    if (value.F && value.M) {
      data.push(value);
    }
  }
  
  return data;
}

function assembleData(category, jsonData, orderBy) {
  "use strict";
  let paramObj = constants.paramObj[category];
  let data = [];
  // let dataMale = []
  // let dataFemale = []
  let dataEu;
  let dataCountries;
  let dataNonMembers;
  let dataEfta;

  dataEu = extractData(constants.countriesEu, jsonData, paramObj, category);

  dataCountries = extractData(
    constants.countriesOthers,
    jsonData,
    paramObj,
    category
  );

  if(dataNonMembers) {
    dataNonMembers = extractData(
      constants.countriesNonMembers,
      jsonData,
      paramObj,
      category
    );
  } 

  dataEfta = extractData(constants.countriesEfta, jsonData, paramObj, category);

  dataCountries.sort(function (a, b) {
    return b[orderBy] - a[orderBy];
  });

  dataEfta.sort(function (a, b) {
    return b[orderBy] - a[orderBy];
  });

  var emptyObj = {};
  emptyObj.country = "";
  emptyObj[constants.sex[0].label] = 0;
  emptyObj[constants.sex[1].label] = 0;

  data = data.concat(dataEu);
  data.push(emptyObj);
  data = data.concat(dataCountries);
  if(dataNonMembers) {
    data.push(emptyObj);
    data = data.concat(dataNonMembers);
  }
  data.push(emptyObj);
  data = data.concat(dataEfta);

  return data;
}

export default function requestData() {
  "use strict";

  // Array of requests
  let requests = [];
  for (let a = 0, n = constants.dataUrls.length; a < n; a += 1) {
    requests.push(queryService(a));
  }

  let defer = $.when.apply($, requests);
  return defer.done(function () {
    // This is executed only after every ajax request has been completed
    for (let a = 0, n = constants.paramObj.length; a < n; a += 1) {
      // const jsonStat = JSONstat(storage.rawData[a]);
      const jsonStat = JSONstat(storage.rawData[constants.dataUrlMapping[a]]);
      const jsonData = jsonStat.Dataset(0);
      if (!jsonData.error) {
        storage.processedDataFemale.push(
          assembleData(a, jsonData, constants.sex[1].abbr)
        );
        storage.processedDataMale.push(
          assembleData(a, jsonData, constants.sex[0].abbr)
        );
      }
    }
  });
}
